

 
.modal {
    position: absolute;
    left: calc(50vw - 200px);
    top: calc(50vh - 200px);
    width: 400px;
    margin-left: auto;
    margin-right: auto;
    border: none;
}

form {
    background: #02298A;
    border-radius: 8px;
    color: white;
}


.dropdown {
    height: 20px;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    font-size: 16px;
    margin: 0;
    padding: 8px;
    width: 100%;
    background-color: #e8eeef;
    color: #8a97a0;
    margin-top: 20px;
    border-radius: 10px;
}

input[type="text"],
input[type="number"] {
    background: rgba(255, 255, 255, 0.1);
    border: none;
    font-size: 16px;
    height: auto;
    margin: 0;
    outline: 0;
    padding: 8px;
    width: 100%;
    background-color: #e8eeef;
    color: #8a97a0;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.03) inset;
    margin-bottom: 20px;
    border-radius: 10px;
}

input[type="radio"],
input[type="checkbox"] {
    margin: 0 4px 8px 0;
}

label {
    display: block;
    margin-bottom: 8px;
}

label.light {
    font-weight: 300;
    display: inline;
}


.statusIcon {
    margin-bottom: 0.25em;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 0.25em;
}


h3 {
    font-size: 0.8em;
}

.vitepay {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #02298A;
    width: 100%;
    padding-bottom: 20px;
}

.qrCode {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.allowMultipleTokensForm {
    margin-top: 20px;
    margin-bottom: 5px;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
}
